
body{margin:0
}
h1{margin:0;font-size:2rem
}
.main-header-heading{margin-left:2em
}
a{color:inherit;text-decoration:inherit
}
#app{font-family:'Roboto', Helvetica, Arial, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;text-align:center;display:-ms-flexbox;display:flex;-ms-flex-direction:column;flex-direction:column;height:100vh
}
header{display:-ms-flexbox;display:flex;background-color:#03a9f4;color:#fefefe;padding:1em;-ms-flex-negative:0;flex-shrink:0
}
header img{margin:-1em;padding:1em;cursor:pointer
}
#app>:nth-child(2){overflow:auto
}
.left header{-ms-flex-pack:center;justify-content:center
}
.left__body{position:fixed;transition:transform 0.2s ease-in;top:0;bottom:0;left:0;width:15em;transform:translateX(-15em);background-color:white
}
.left--is-open .left__body{transform:translateX(0)
}
.left__back-drop{position:fixed;top:0;bottom:0;left:0;right:0;background-color:rgba(255,255,255,0.5);height:100%;width:100%;display:none
}
.left--is-open .left__back-drop{display:block
}
.left__link{padding:0.5em;font-size:1.5rem;display:block;border-bottom:1px solid #ccc
}



