
h3{margin:0
}
.result{background-color:#ddd;list-style-type:none;padding:0;margin:0;overflow:auto;height:100%
}
.result-item{margin:0.5rem;padding:0.5rem;border-radius:0.25em;background-color:#fff
}
.result .preview-head{display:-ms-flexbox;display:flex
}
.expander{border-radius:50%;height:2rem;width:2rem;font-size:1.5rem;background-color:#03a9f4;color:#fff;border:0;margin-left:auto
}
.expander--minus{font-size:2rem
}
.expander--minus>*{margin-top:-6px;display:block
}
.expander img{width:1.25rem;margin-top:0.17em
}



