
.quick-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.field-wrapper {
  position: relative;
}
.typeahead-result {
  position: absolute;
  padding: 0;
  top: 2rem;
  max-width: 20rem;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  background-color: #fff;
  list-style: none;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0, 0, 0, .25);
  z-index: 1000;
  max-height: 20rem;
  overflow: auto
}
.typeahead-result-item {
  padding: 1em;
  font-size: 1rem;
  border-bottom: 1px solid #ccc;
}
.typeahead-result-item--active {
  background-color: #ccc;
}



